$primary: #ff6d00;
$primary--light: #ff9e40;
$primary--dark: #c43c00;

$secondary: #212121;
$secondary--light: #484848;
$secondary--dark: #000;

$text--dark: #000;
$text--light: #fff;

$h2: 1.5rem;
$h3: 1.2rem;
$indent: 1.5rem;
@import url('https://fonts.googleapis.com/css?family=Fira+Mono:400,500,700');

body {
  margin: 0;
  padding: 0;
  font-family: "Fira Mono", monospace;
  background-color: $secondary;
}

a {
  color: $primary;
  text-decoration: none;

  &:hover {
    color: $primary--light;
  }
}

h1, h2, h3, h4, p, small, li {
  margin-top: 0;
  &:not(:first-child) {
    margin-top: 0.2rem;
  }
  margin-bottom: 0;
  font-weight: normal;
}

ul {
  margin: 0.2rem 0;
  padding: 0 $indent;
  list-style: none;
}

h2 {
  size: $h2;
}

h3 {
  size: $h3;
  color: $primary--light;
}

small {
  display: block;
}


